import Lqxq from '../views/view/lqxq.vue'       //
import Tgewm from '../views/view/tgewm.vue'     //
import Tglb from '../views/view/tglb.vue'       //
import Tjyhk from '../views/view/tjyhk.vue'     //
import Tx from '../views/view/tx.vue'           // *
import Setpassword from '../views/view/setpassword.vue' //
import Setup from '../views/view/setup.vue'  //   *
