import Qxx from '../views/view/qxx.vue'   //
import Cz from '../views/view/cz.vue' //
import Tc from '../views/view/tanchuang/tanchuang.vue' //
import Fhb from '../views/view/fhb.vue'  //
import Information from '../views/information.vue' //*
import Wd from '../views/wd.vue' // *
