<template>
  <div class="app">
    <div class="header">
      <img src="../assets/fanhuibai.png" @click="fanhui">
        <span>
          
        </span>
    </div>
    <div class="kuai">
      <span>*</span> 
      <input type="text" id="tell" placeholder="" @blur="settell">
    </div>
    <div class="kuai">
      <span>*</span>
      <input type="text" id="tell" placeholder="" @blur="setyzm">
      <button v-if="yzmbj" @click="fsyzm"></button>
      <button v-if="!yzmbj">{{yzmnum}}</button>
    </div>
    <div class="kuai">
      <span>*</span>
      <input type="password" id="tell" placeholder="(6~12)" @blur="getword">
    </div>
    <div class="kuai">
      <span>*</span>
      <input type="text" id="tell" placeholder="" @blur="setnickname">
    </div>
    <div class="kuai">
      <span></span>
      <input type="text" id="tell" placeholder="" @blur="setcode">
    </div>
      <button id="submit" @click="sub"></button>
  </div>
</template>
<script>
  const axios = require('axios');
  import { loginIn} from "../utils/auth";
  import { Url } from "../utils/config"
export default {
  data(){
    return{
      tell:'',//
      password:'',//
      user_nickname:'',//
      code:'',//
      yzm:'',//
      getyzm:'',//
      yzmnum:60,//
      yzmbj:true,//
    }
  },
  
  methods:{
    //
     fanhui:function(){
        this.$router.back(-1);
    },
    //
    settell:function(e){
      this.tell = e.srcElement.value;
      var _this = this;
    },
    //
    getword:function(e){
      this.password = e.srcElement.value;
    },
    //
    fsyzm:function(){
      axios({
        url:Url+"/apis/user/sendSms",
        method:"post",
        params:{
          user_login:this.tell,
          send_type: 1,
        }
      }).then(function(res){
        // console.log(res)
        // console.log(res.data.data)
        // if(res.data.code == 1){
        //   console.log(res.data.data)
        //   this.getyzm = res.data.data;
        // }else{
        //   this.$dialog.alert({
        //     title: '',
        //     message: res.data.msg
        //   })
        // }
      }.bind(this))
      this.yzmbj = false;
      var shuzi = 59;
      var djs = setInterval(()=>{
        this.yzmnum = shuzi--;
        console.log(this.yzmnum)
        if(this.yzmnum==0){
        clearInterval(djs);
        this.yzmnum = 60;
        this.yzmbj = true;
      }
      },1000)
      
    },
    //
    setyzm:function(e){
      this.yzm = e.srcElement.value;
    },
    //
    setnickname:function(e){
      this.user_nickname = e.srcElement.value;
    },
    //
    setcode:function(e){
      this.code = e.srcElement.value;
    },

    sub:function(){
      var _this = this;
       axios({
        url:Url+"/apis/user/validateMobile",
       
        method:"post",
        params:{
          send_type:1,
          user_login:this.tell
   
        }
      }).then(function(res){
        console.log(res.data);
        if(res.data.code == 0){
          //alert(res.data.msg)
           _this.$dialog.alert({
              title: '',
              message: res.data.msg
            })
        }
      })
      // if(this.yzm == this.getyzm){
        if(this.tell && this.password && this.user_nickname && this.yzm){
          axios({
            url:Url+"/apis/user/register",
            method:"post",
            params:{
              user_login:this.tell,
              passwd: this.password,
              user_nickname:this.user_nickname,
              code:this.code,
              code_yz:this.yzm
            }
          }).then(function(res){
            console.log(res.data)
            if(res.data.code == 1){
              loginIn("Id",res.data.data);
              _this.$dialog.alert({
                title: '',
                message: ''
              }).then(function(){
                  _this.$router.push({
                  path:"/index"
                })
              })
            }else{
              _this.$dialog.alert({
                title: '',
                message: res.data.msg
              }).then(function(){
                // on close
              })
            }
          }.bind(this).bind(this))
            }else{
                alert("*");
              }
            // }else{
            //   alert("")
            // }
        }
      }
}
</script>
<style scoped>
  .app{
    width: 100%;
    height: 100%;
    background-image: url(../assets/beijing.png);
    position: fixed;
  }
  .header{
    width:100%;
    height: 0.87rem;
    position: relative;
    z-index: 4;
    background-image: url(../assets/beijing.png);
  }
    .header img{
        display: block;
        width: 0.2rem;
        height: 0.34rem;
        margin: 0.29rem 0 0 0.5rem;
        float: left;
    }
    .header span{
        display: block;
        width: 3rem;
        height: 0.3rem;
        font-size: 0.3rem;
        margin-top: 0.25rem;
        margin-left: 0.2rem;
        float: left;
        color: #fff;
    }
    .kuai{
      width: 5.83rem;
      height: 0.75rem;
      margin: 0 auto;
      border-bottom: 0.01rem solid #fff;
      position: relative;
      top: 1.5rem;
      margin-bottom: 0.33rem;
    }
    .kuai span{
      width: 1.39rem;
      height: 0.75rem;
      color: #fff;
      display: block;
      line-height: 0.75rem;
      font-size: 0.28rem;
      float: left;
      
    }
    .kuai input{
      border: 0;
      width: 2.7rem;
      background:rgba(255, 255, 255, 0);
      display: block;
      float: left;
      color: #fff;
      font-size: 0.28rem;
      position: relative;
      top: 0.2rem;
    }
    .kuai input::placeholder{
      color: #fff;
    }
    .kuai button{
      height: 0.75rem;
      border: 0;
      background:rgba(255, 255, 255, 0);
      display: block;
      float:right;
      font-size: 0.24rem;
      color:#fff;
    }
    #submit{
      border: 0;
      width: 5.87rem;
      height: 0.8rem;
      margin: 0 auto;
      background: #fff;
      border-radius: 0.08rem;
      font-size: 0.28rem;
      color: #0AC161;
      position: relative;
      top: 1.64rem;
      left: 0.84rem;
    }
</style>



